d.length !== 0\n        ? axios.get(targetUrl, { params: formattedParams }).then(({ data }) => {\n            this.mapData = data.data;\n          })\n        : (this.mapData = []);\n    },\n\n    handleResourceFilter: function (selectedFilter) {\n      this.filter.resource_type_id = selectedFilter;\n    },\n  },\n  computed: {\n    ward() {\n      return this.$store.getters.ward;\n    },\n  },\n};\n</script>\n\n<style scoped>\n.institution {\n  display: flex;\n}\n.map-filter {\n  list-style: none;\n}\n</style>\n"],"sourceRoot":""}]);
